.map-container{
    position: relative;
    width: 98%;
    height:98%;
    border-radius:5vh;
    overflow: hidden;    
    border:1px solid #ccc;
    -webkit-box-shadow:0px 0px 30px 5px #fff4;
            box-shadow:0px 0px 30px 5px #fff4;
    margin:1%;
    -ms-flex: 1 1;
        flex: 1 1;
}

.map {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 5vh;
}

.map-container p{
    position: absolute;
    z-index: 1;
    bottom: 1vh;
    left: 1vw;
}


